      <div class="return" @click="showFlag = false" ><i class="fa fa-close"></i></div>
      <div class="json">
        <tree-view :data="json" :options="{rootObjectKey: 'SVG'}"></tree-view>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  props: {
    json: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {
      showFlag: false
    }
  },
  methods: {
    show() {
      this.showFlag = true;
    }
  }
}
</script>

