<template>
  <div class="v-canvas">
    <canvas id="canvas" width="1000" height="1000"></canvas>
  </div>  
</template>

<script>
import * as Draw from '@/js/draw.js';

let intervalId;
let doAnimate = [];
let animates = [];
export default {
  mounted() {
    let canvas = document.getElementById('canvas');
    this.ctx = canvas.getContext('2d');
    this.ctx.save();
    canvas.setAttribute('width', '2000'); // window.innerWidth / 2 - 4 + ''
    canvas.setAttribute('height', '2000'); // window.innerHeight - 54 + ''
    canvas.style.left = 0;
    canvas.style.top = 0;
    this.moveCanvas();
  },

  methods: {
    // 鼠标拖动
    moveCanvas() {
      var disX = 0, disY = 0;
      var canvas = document.getElementById('canvas');
      canvas.onmousedown = function(ev) {
        let oEvent = ev || event;
        disX = oEvent.clientX - canvas.offsetLeft; // 鼠标在 canvas 内距离 canvas 左端的距离
        disY = oEvent.clientY - canvas.offsetTop; // 鼠标在 canvas 内距离 canvas 上端的距离
        document.onmousemove = function(ev) {
          let oEvent = ev || event;
          let currentLeft = oEvent.clientX - disX;
          let currentTop = oEvent.clientY - disY;
          canvas.style.left = currentLeft + 'px';
          canvas.style.top = currentTop + 'px';
        };
        document.onmouseup = function(ev) {
          document.onmousemove = null;
          document.onmouseup = null;
        };
        return false;
      };
    },
    drawArc (cx, cy, radiusX, radiusY, rotation, startAngle, endAngle) {
      this.ctx.beginPath();
      Draw.drawArc(this.ctx, cx, cy, radiusX, radiusY, rotation, startAngle, endAngle);
    },
    // 画圆
    drawCircle (cx, cy, r, stroke, strokeWidth, fill) {
      this.ctx.beginPath();
      Draw.drawCircle(this.ctx, cx, cy, r, stroke, strokeWidth, fill);
    },

    // 画椭圆
    drawEllipse (cx, cy, rx, ry, stroke, strokeWidth, fill) {
      this.ctx.beginPath();
      Draw.drawEllipse(this.ctx, cx, cy, rx, ry, stroke, strokeWidth, fill);
    },

    // 画矩形
    drawRectangle (x, y, width, height, stroke, strokeWidth, fill) {
      this.ctx.beginPath();
      Draw.drawRectangle(this.ctx, x, y, width, height, stroke, strokeWidth, fill);
    },

    // 画直线
    drawLine (x1, y1, x2, y2, stroke, strokeWidth) {
      this.ctx.beginPath();
      Draw.drawLine(this.ctx, x1, y1, x2, y2, stroke, strokeWidth);
    },

    // 画多边形或者折线
    drawPoly (points, stroke, strokeWidth, fill, type) {
      this.ctx.beginPath();
      Draw.drawPoly(this.ctx, points, stroke, strokeWidth, type);
      if (fill && type !== 0) {
        this.ctx.fillStyle = fill;
        this.ctx.fill();
      }
    },
    // 画路径
    drawPath (points, stroke, strokeWidth, fill) {
      this.ctx.beginPath();
      Draw.drawPath(this.ctx, points, stroke, strokeWidth, fill);
      this.ctx.closePath();
    },

    // 渐变
    createLinearGradient (id, x1='0%', y1='0%', x2='100%', y2='0%') {
      Draw.createLinearGradient(id, x1, y1, x2, y2)
    },

    createRadialGradient (id, cx="50%", cy='50%', r='50%', fx=cx, fy=cy) {
      Draw.createRadialGradient(id, cx, cy, r, fx, fy)
    },

    drawGradient (id, offset, stopColor) {
      Draw.drawGradient (id, offset, stopColor);
    },

    //输出文本
    drawText (x, y, fill, characters,fontSize) {
      this.ctx.beginPath();
      Draw.drawText (this.ctx,x,y,fill,characters,fontSize);
    },

    drawCubicBezier (x0, y0, cp1x, cp1y, cp2x, cp2y, x1, y1, stroke, strokeWidth) {
      Draw.drawCubicBezier(this.ctx, x0, y0, cp1x, cp1y, cp2x, cp2y, x1, y1, stroke, strokeWidth)
    },

    drawQuadraticBezier (x0, y0, cpx, cpy, x1, y1, stroke, strokeWidth) {
      Draw.drawQuadraticBezier(this.ctx, x0, y0, cpx, cpy, x1, y1, stroke, strokeWidth)
    },

    computeAnimate() {
      let result = {};
      for(let i in animates) {
        let time = animates[i]['dur'].substring(0,  animates[i]['dur'].length - 1);
        time *= 100;
        let resultMap = {};
        resultMap['step'] = time;
        resultMap['count'] = 0;
        if(animates[i]['attribute'] === 'fill' || animates[i]['attribute'] === 'stroke') {
          this.gradientColor(animates[i]['from'], animates[i]['to'], time, resultMap);
          result[animates[i]['attribute']] = resultMap
        } else {
          let distance = animates[i]['to'] - animates[i]['from'];
          distance /= time;
          resultMap['distance'] = distance;
          result[animates[i]['attribute']] = resultMap
        }
      }
      return result;
    },

    animate(attributeName, fromTime, to, dur) {
        let animateSet = {};
        animateSet['attribute'] = attributeName;
        animateSet['from'] = fromTime;
        animateSet['to'] = to;
        animateSet['dur'] = dur;
        animates.push(animateSet);
    },

    addAnimate(origin) {
      let result = this.computeAnimate();
      if(animates.length !== 0) {
        animates = [];
      }
      // clear
      let change = {
        cx : origin['cx'],
        cy : origin['cy'],
        r  : origin['r'],
        rx : origin['rx'],
        ry : origin['ry'],
        x  : origin['x'],
        y  : origin['y'],
        height : origin['height'],
        width  : origin['width'],
        stroke : origin['stroke'],
        fill : origin['fill'],
        strokeWidth : origin['stroke-width']
      };

      let doAnimateItem = {};
      doAnimateItem['type'] = origin['type'];
      doAnimateItem['change'] = change;
      doAnimateItem['result'] = result;
      doAnimateItem['origin'] = origin;
      doAnimate.push(doAnimateItem);
    },

    drawAnimate() {
      let canvas = document.getElementById('canvas');
      let imageData = this.ctx.getImageData(0, 0, canvas.width, canvas.height);

      intervalId = setInterval(() => {
        this.clearCanvas();
        this.viewAnimate(imageData);
      }, 10);
    },

    viewAnimate(imageData) {
      this.ctx.putImageData(imageData, 0, 0);
      for(let i in doAnimate) {
        this.changeByStep(doAnimate[i]['change'], doAnimate[i]['result'], doAnimate[i]['origin'])
      }
      for(let i in doAnimate) {
        let cx = doAnimate[i]['change']['cx'];
        let cy = doAnimate[i]['change']['cy'];
        let r  = doAnimate[i]['change']['r'];
        let rx = doAnimate[i]['change']['rx'];
        let ry = doAnimate[i]['change']['ry'];
        let x = doAnimate[i]['change']['x'];
        let y = doAnimate[i]['change']['y'];
        let height = doAnimate[i]['change']['height'];
        let width = doAnimate[i]['change']['width'];
        let stroke = doAnimate[i]['change']['stroke'];
        let strokeWidth = doAnimate[i]['change']['stroke-width'];
        let fill = doAnimate[i]['change']['fill'];

        switch (doAnimate[i]['type']) {
          case 'circle' :
            Draw.drawCircle(this.ctx, cx, cy, r, stroke, strokeWidth, fill);
            break;
          case 'ellipse' :
            Draw.drawEllipse(this.ctx, cx, cy, rx, ry, stroke, strokeWidth, fill);
            break;
          case 'rect' :
            Draw.drawRectangle(this.ctx, x, y, width, height, stroke, strokeWidth, fill);
        }
      }
    },

    changeByStep(change, result, origin) {
      if(result['cx']) {
        change.cx += result['cx']['distance'];
        result['cx']['count'] += 1;
        if(result['cx']['count'] === result['cx']['step']) {
          result['cx']['count'] = 0;
          change.cx = origin['cx'];
        }
      }

      if(result['cy']) {
        change.cy += result['cy']['distance'];
        result['cy']['count'] += 1 ;
        if(result['cy']['count'] === result['cy']['step']) {
          result['cy']['count'] = 0;
          change.cy = origin['cy'];
        }
      }

      if(result['r']) {
        change.r += result['r']['distance'];
        result['r']['count'] += 1 ;
        if(result['r']['count'] === result['r']['step']) {
          result['r']['count'] = 0;
          change.r = origin['r'];
        }
      }

      if(result['rx']) {
        change.rx += result['rx']['distance'];
        result['rx']['count'] += 1;
        if(result['rx']['count'] === result['rx']['step']) {
          result['rx']['count'] = 0;
          change.rx = origin['rx'];
        }
      }

      if(result['ry']) {
        change.ry += result['ry']['distance'];
        result['ry']['count'] += 1;
        if(result['ry']['count'] === result['ry']['step']) {
          result['ry']['count'] = 0;
          change.ry = origin['ry'];
        }
      }


      if(result['x']) {
        change.x += result['x']['distance'];
        result['x']['count'] += 1;
        if(result['x']['count'] === result['x']['step']) {
          result['x']['count'] = 0;
          change.x = origin['x'];
        }
      }

      if(result['y']) {
        change.y += result['y']['distance'];
        result['y']['count'] += 1;
        if(result['y']['count'] === result['y']['step']) {
          result['y']['count'] = 0;
          change.y = origin['y'];
        }
      }


      if(result['width']) {
        change.width += result['width']['distance'];
        result['width']['count'] += 1;
        if(result['width']['count'] === result['width']['step']) {
          result['width']['count'] = 0;
          change.width = origin['width'];
        }
      }

      if(result['height']) {
        change.height += result['height']['distance'];
        result['height']['count'] += 1;
        if(result['height']['count'] === result['height']['step']) {
          result['height']['count'] = 0;
          change.height = origin['height'];
        }
      }

      if(result['stroke-width']){
        change['stroke-width'] += result['stroke-width']['distance'];
        result['stroke-width']['count'] += 1;
        if(result['stroke-width']['count'] === result['stroke-width']['step']) {
          result['stroke-width']['count'] = 0;
          change['stroke-width'] = origin['stroke-width'];
        }
      }

      if(result['fill']) {
        let temp = change.fill;
        let fill = this.colorRgb(temp);
        fill[0] += result['fill']['sR'];
        fill[1] += result['fill']['sB'];
        fill[2] += result['fill']['sG'];
        change.fill = this.combineRbg(fill);
        result['fill']['count'] += 1;
        if(result['fill']['count'] === result['fill']['step']) {
          result['fill']['count'] = 0;
          change.fill = origin['fill'];
        }
      }

      if(result['stroke']) {
        let temp_s = change.stroke;
        let stroke = this.colorRgb(temp_s);
        stroke[0] += result['stroke']['sR'];
        stroke[1] += result['stroke']['sB'];
        stroke[2] += result['stroke']['sG'];
        change.stroke = this.combineRbg(stroke);
        result['stroke']['count'] += 1;
        if(result['stroke']['count'] === result['stroke']['step']) {
          result['stroke']['count'] = 0;
          change.stroke = origin['stroke'];
        }
      }
    },

    gradientColor(startColor, endColor, step, resultMap) {
      let startRGB = this.colorRgb(startColor);//转换为rgb数组模式
      let startR = startRGB[0];
      let startG = startRGB[1];
      let startB = startRGB[2];

      let endRGB = this.colorRgb(endColor);
      let endR = endRGB[0];
      let endG = endRGB[1];
      let endB = endRGB[2];

      let sR = (endR - startR) / step;//总差值
      let sG = (endG - startG) / step;
      let sB = (endB - startB) / step;

      resultMap['sR'] = sR;
      resultMap['sG'] = sG;
      resultMap['sB'] = sB;
    },

    colorRgb(color) {
      let colorRbg = [];
      let temp;

      temp = color.substring(1,3);
      colorRbg[0] = parseInt(temp, 16);
      temp = color.substring(3,5);
      colorRbg[1] = parseInt(temp, 16);
      temp = color.substring(5,7);
      colorRbg[2] = parseInt(temp, 16);

      return colorRbg;
    },

    combineRbg(colorRbg) {
      let R = Math.round(colorRbg[0]).toString(16);
      if(colorRbg[0] < 16) { R = '0' + R }
      let B = Math.round(colorRbg[1]).toString(16);
      if(colorRbg[1] < 16) { B = '0' + B }
      let G = Math.round(colorRbg[2]).toString(16);
      if(colorRbg[2] < 16) { G = '0' + G }
      return '#'+ R + B + G;
    },
      // 清空当前窗口
    clearCanvas () {
      let canvas = document.getElementById('canvas');
      this.ctx.clearRect(0, 0, canvas.width, canvas.height);
    },

    clearCanvasComplete () {
      doAnimate = [];
      animates = [];
      clearInterval(intervalId);
      let canvas = document.getElementById('canvas');
      this.ctx.clearRect(0, 0, canvas.width, canvas.height);
    }
  }
}
</script>

<style lang="stylus" scoped>
.v-canvas
  display: inline-block
  #canvas
    position: absolute
    border: 3px solid rgba(0, 123, 255, 0)
    transition: opacity 0.2s ease-in-out
    transition: border 0.2s ease-in-out
    &:hover
      cursor: move
      border: 3px dashed rgba(0, 123, 255, 0.3)
</style>


