/**
* 代表错误类型.
* @class Eroor
* @constructor
* @param {int} line - 出现错误的行数.
* @param {string} content - 错误的内容.
* @param {string} message - 错误的提示信息.
*/
function Error (line, content, message) {
  this.line = line;
  this.content = content;
  this.message = message;
  this.print = function () {
    let output = "行数:" + this.line + "\n出错内容:" + this.content + "\n错误类型:" + this.message;
    return output;
  };
}

function GrammarError (content, message) {
  this.content = content;
  this.message = message;
  this.print = function () {
    return {
      content: this.content, // '\n错误类型为'
      message: this.message
    };
  };
}
export {
  Error,
  GrammarError
};
