// 渐变
var Gradient = {};
function createLinearGradient (id, x1, y1, x2, y2) {
  // console.log('drawGradient')
  var newGradient = {};
  newGradient['id'] = id;
  Gradient[id] = newGradient;
  newGradient['type'] = 'linear';
  newGradient['style'] = [];
  newGradient['x1'] = x1;
  newGradient['y1'] = y1;
  newGradient['x2'] = x2;
  newGradient['y2'] = y2;
  // ctx.beginPath();
  // let grad = ctx.createLinearGradient(0, 100, 300, 100);
  // grad.addColorStop(0, "rgb(0, 255, 0)");
  // grad.addColorStop(1, "rgb(255, 0, 0)");
  // ctx.fillStyle = grad;
  // ctx.fillRect(0, 100, 300, 200);
  // ctx.closePath();
}

function createRadialGradient (id, cx, cy, r, fx, fy) {
  console.log('drawGradient');
  var newGradient = {};
  newGradient['id'] = id;
  Gradient[id] = newGradient;
  newGradient['type'] = 'radial';
  newGradient['style'] = [];
  newGradient['cx'] = cx;
  newGradient['cy'] = cy;
  newGradient['r'] = r;
  newGradient['fx'] = fx;
  newGradient['fy'] = fy;
  // ctx.beginPath();
  // let grad = ctx.createLinearGradient(0, 100, 300, 100);
  // grad.addColorStop(0, "rgb(0, 255, 0)");
  // grad.addColorStop(1, "rgb(255, 0, 0)");
  // ctx.fillStyle = grad;
  // ctx.fillRect(0, 100, 300, 200);
  // ctx.closePath();
}

// 填充渐变
function drawGradient (id, offset, stopColor) {
  if (Gradient) {
    for (let i in Gradient) {
      if (Gradient[i].id === id) {
        let newOffset = percentToDouble(offset);
        let style = {};
        style['offset'] = newOffset;
        style['stopColor'] = stopColor;

        Gradient[i]['style'].push(style);
        break;
      }
    }
  }
}

// 画圆
function drawCircle (ctx, cx, cy, r, stroke, strokeWidth, fill) {
  ctx.beginPath();
  ctx.arc(cx, cy, r, 0, 360, false);
  ctx.fillStyle = fill;

  if (fill && fill.match(/url\(#[a-zA-Z]*\)/)) {
    let id = fill.substring(5, fill.length - 1);
    let idFill;
    for (let i in Gradient) {
      if (Gradient[i].id === id) {
        if (Gradient[i].type === 'linear') {
          let x1 = percentToDouble(Gradient[i].x1);
          let y1 = percentToDouble(Gradient[i].y1);
          let x2 = percentToDouble(Gradient[i].x2);
          let y2 = percentToDouble(Gradient[i].y2);
          idFill = ctx.createLinearGradient((cx - r) + Math.round(x1 * 2 * r), (cy - r) + Math.round(y1 * 2 * r), (cx - r) + Math.round(x2 * 2 * r), (cy - r) + Math.round(y2 * 2 * r));
          // idFill = ctx.createLinearGradient(x1, y1, x2, y2);
          for (let j = 0; j < Gradient[i]['style'].length; ++j) {
            idFill.addColorStop(Gradient[i]['style'][j]['offset'], Gradient[i]['style'][j]['stopColor']);
          }
        } else {
          let x1 = percentToDouble(Gradient[i].cx);
          let y1 = percentToDouble(Gradient[i].cy);
          let dr = percentToDouble(Gradient[i].r);
          // let fx = Gradient[i].fx;
          // let fy = Gradient[i].fy;
          idFill = ctx.createRadialGradient((cx - r) + Math.round(x1 * 2 * r), (cy - r) + Math.round(y1 * 2 * r), dr, (cx - r) + Math.round(x1 * 2 * r), (cy - r) + Math.round(y1 * 2 * r), r);

          for (let j = 0; j < Gradient[i]['style'].length; ++j) {
            idFill.addColorStop(Gradient[i]['style'][j]['offset'], Gradient[i]['style'][j]['stopColor']);
          }
        }

        ctx.fillStyle = idFill;
        break;
      }
    }
  } else if (fill) {
    ctx.fillStyle = fill;
  } else {
    ctx.fillStyle = stroke;
  }

  ctx.fill();
  if (stroke) {
    ctx.strokeStyle = stroke;
    ctx.lineWidth = strokeWidth;
    ctx.stroke();
  }

  ctx.closePath();
}

// 画椭圆
function drawEllipse (ctx, cx, cy, rx, ry, stroke, strokeWidth, fill) {
  let k = 0.5522848;
  let ox = rx * k; // 水平控制点偏移量
  let oy = ry * k; // 垂直控制点偏移量

  ctx.beginPath();
  ctx.moveTo(cx - rx, cy);
  ctx.bezierCurveTo(cx - rx, cy - oy, cx - ox, cy - ry, cx, cy - ry);
  ctx.bezierCurveTo(cx + ox, cy - ry, cx + rx, cy - oy, cx + rx, cy);
  ctx.bezierCurveTo(cx + rx, cy + oy, cx + ox, cy + ry, cx, cy + ry);
  ctx.bezierCurveTo(cx - ox, cy + ry, cx - rx, cy + oy, cx - rx, cy);

  if (fill && fill.match(/url\(#[a-zA-Z]*\)/)) {
    let id = fill.substring(5, fill.length - 1);
    let idFill;
    for (let i in Gradient) {
      if (Gradient[i].id === id) {
        let x1 = percentToDouble(Gradient[i].x1);
        let y1 = percentToDouble(Gradient[i].y1);
        let x2 = percentToDouble(Gradient[i].x2);
        let y2 = percentToDouble(Gradient[i].y2);
        idFill = ctx.createLinearGradient((cx - rx) + Math.round(x1 * 2 * rx), (cy - ry) + Math.round(y1 * 2 * ry), (cx - rx) + Math.round(x2 * 2 * rx), (cy - ry) + Math.round(y2 * 2 * ry));

        for (let j = 0; j < Gradient[i]['style'].length; ++j) {
          idFill.addColorStop(Gradient[i]['style'][j]['offset'], Gradient[i]['style'][j]['stopColor']);
        }
        ctx.fillStyle = idFill;

        break;
      }
    }
  } else if (fill) {
    ctx.fillStyle = fill;
  } else {
    ctx.fillStyle = stroke;
  }

  ctx.fill();
  if (stroke) {
    ctx.strokeStyle = stroke;
    ctx.lineWidth = strokeWidth;
    ctx.stroke();
  }

  ctx.closePath();
}

// 画矩形
function drawRectangle (ctx, x, y, width, height, stroke, strokeWidth, fill) {
  ctx.beginPath();
  ctx.rect(x, y, width, height);

  if (fill && fill.match(/url\(#[a-zA-Z]*\)/)) {
    let id = fill.substring(5, fill.length - 1);
    let idFill;
    for (let i in Gradient) {
      if (Gradient[i].id === id) {
        let x1 = percentToDouble(Gradient[i].x1);
        let y1 = percentToDouble(Gradient[i].y1);
        let x2 = percentToDouble(Gradient[i].x2);
        let y2 = percentToDouble(Gradient[i].y2);
        idFill = ctx.createLinearGradient(x + Math.round(x1 * width), y + Math.round(y1 * height), x + Math.round(x2 * width), y + Math.round(y2 * height));

        for (let j = 0; j < Gradient[i]['style'].length; ++j) {
          idFill.addColorStop(Gradient[i]['style'][j]['offset'], Gradient[i]['style'][j]['stopColor']);
        }
        ctx.fillStyle = idFill;
        break;
      }
    }
  } else if (fill) {
    ctx.fillStyle = fill;
  } else {
    ctx.fillStyle = stroke;
  }

  ctx.fill();
  if (stroke) {
    ctx.strokeStyle = stroke;
    ctx.lineWidth = strokeWidth;
    ctx.stroke();
  }
  ctx.closePath();
}

// 画直线
function drawLine (ctx, x1, y1, x2, y2, stroke, strokeWidth) {
  ctx.lineTo(x1, y1);
  ctx.lineTo(x2, y2);
  if (stroke) {
    ctx.strokeStyle = stroke;
    ctx.lineWidth = strokeWidth;
    ctx.stroke();
  }
}

// 画弧线
function drawArc (ctx, x, y, radiusX, radiusY, rotation, startAngle, endAngle, stroke, strokeWidth, clockwise) {
  // 椭圆圆心x，y
  // radiusX, 长半轴长度
  // radiusY, 长半轴长度
  // rotation, 椭圆旋转角度 (单位是度不是弧度)
  // startAngle, 椭圆弧起始角弧度 (单位是弧度不是度!)
  // endAngle, 椭圆弧结束角弧度 (单位是弧度不是度!)
  ctx.ellipse(x, y, radiusX, radiusY, rotation, startAngle, endAngle, clockwise);
  if (stroke) {
    ctx.strokeStyle = stroke;
    ctx.lineWidth = strokeWidth;
    ctx.stroke();
  }
}

// 画多边形或者折线
function drawPoly (ctx, points, stroke, strokeWidth, type) {
  ctx.moveTo(points[0].x, points[0].y);
  for (let i = 1; i < points.length; i++) {
    ctx.lineTo(points[i].x, points[i].y);
  }
  if (type !== 0) {
    console.log(type);
    ctx.lineTo(points[0].x, points[0].y);
  }
  if (stroke) {
    ctx.strokeStyle = stroke;
    ctx.lineWidth = strokeWidth;
    ctx.stroke();
  }
}

// 画二次贝塞尔曲线
function drawQuadraticBezier (ctx, x0, y0, cpx, cpy, x1, y1, stroke, strokeWidth) {
  ctx.lineTo(x0, y0);
  ctx.quadraticCurveTo(cpx, cpy, x1, y1);
  if (stroke) {
    ctx.strokeStyle = stroke;
    ctx.lineWidth = strokeWidth;
    ctx.stroke();
  }
}

// 画三次贝塞尔曲线
function drawCubicBezier (ctx, x0, y0, cp1x, cp1y, cp2x, cp2y, x1, y1, stroke, strokeWidth) {
  ctx.lineTo(x0, y0);
  ctx.bezierCurveTo(cp1x, cp1y, cp2x, cp2y, x1, y1);
  if (stroke) {
    ctx.strokeStyle = stroke;
    ctx.lineWidth = strokeWidth;
    ctx.stroke();
  }
}

function drawPath (ctx, points, stroke, strokeWidth, fill) {
  let first = 0;
  ctx.beginPath();
  for (let i = 0; i < points.length; i++) {
    switch (points[i].flag) {
      case 'M': // 移动点
        ctx.moveTo(points.x, points.y);
        break;
      case 'Q': // 二次贝塞尔曲线
        drawQuadraticBezier(ctx, points[i - 1].x, points[i - 1].y, points[i].x1, points[i].y1, points[i].x, points[i].y, stroke, strokeWidth);
        break;
      case 'C': // 三次贝塞尔曲线
        drawCubicBezier(ctx, points[i - 1].x, points[i - 1].y, points[i].x1, points[i].y1, points[i].x2, points[i].y2, points[i].x, points[i].y, stroke, strokeWidth);
        break;
      case 'L': // 任意直线
        drawLine(ctx, points[i].x1, points[i].y1, points[i].x, points[i].y, stroke, strokeWidth);
        break;
      case 'A': // 画椭圆弧
        drawArc(ctx, points[i].cx, points[i].cy, points[i].radiusX, points[i].radiusY, points[i].rotation, points[i].startAngle, points[i].endAngle, stroke, strokeWidth, points[i].clockwise);
        break;
      case 'Z':
        drawLine(ctx, points[i - 1].x, points[i - 1].y, points[first].x, points[first].y, stroke, strokeWidth);
        first = i + 1;
        ctx.closePath();
        ctx.fillStyle = fill;
        ctx.fill();
        ctx.beginPath();
        break;
    }
  }
}

/* 描绘文本
 *@method drawText
 *@param{canvas}ctx 在该画布上描绘
 *@param{int}x 文本的x坐标点
 *@param{int}y 文本的y坐标点
 *@param{string|null} fill 文字填充颜色
 *@param{string}characters 文本内容
 *@param{string}fontSize 文字大小
*/
function drawText (ctx, x, y, fill, characters, fontSize) {
  if (fill === 'transparent') { fill = 'black'; }
  if (fontSize === 'transparent') { fontSize = '24px'; }
  ctx.fillStyle = fill;
  ctx.fill();
  ctx.font = fontSize + ' Arial';

  // 多个坐标点的情况
  if ((/,[0-9]+/.test(x)) && (/,[0-9]+/.test(y))) {
    x = x.split(',');
    y = y.split(',');
    let tmp = 0;
    for (tmp = 0; tmp < x.length; tmp++) {
      ctx.fillText(characters[tmp], x[tmp], y[tmp]);
    }
    if (tmp < characters.length) {
      characters = characters.substring(tmp - 1, characters.length);
      drawText(ctx, x[tmp - 1], y[tmp - 1], fill, characters);
    }
  } else if (/,[0-9]+/.test(x)) {
    x = x.split(',');
    let tmp = 0;
    for (tmp = 0; tmp < x.length; tmp++) {
      ctx.fillText(characters[tmp], x[tmp], y);
    }
  } else if (/,[0-9]+/.test(y)) {
    y = y.split(',');
    let tmp = 0;
    for (tmp = 0; tmp < y.length; tmp++) {
      ctx.fillText(characters[tmp], x, y[tmp]);
    }
  } else {
    ctx.fillText(characters, x, y);
  }

  ctx.stroke();
  ctx.closePath();
}

function percentToDouble (str) {
  if (typeof str === 'number') {
    return str;
  }
  let newStr = str.substring(0, str.length - 1);
  newStr /= 100;

  return newStr;
}

export { drawCircle, drawText, drawEllipse, drawRectangle, drawLine, drawPoly, drawArc, drawPath, drawQuadraticBezier, drawCubicBezier, createLinearGradient, createRadialGradient, drawGradient };
