import { svgArcToCenterParam } from '../lib/pathA';
import { GrammarError } from '../lib/error';

const apprPI = 6.283185292278425;

function path (realValue) {
  realValue = realValue.replace(/[,]/g, ' ');
  realValue = realValue.replace(/[-]/g, ' -');
  let pathArray = realValue.match(/[mMlLhHvVcCsSqQtTaAzZ]([- .0-9]*)*/g);

  let point = [];
  if (pathArray[0][0] !== 'M' && pathArray[0][0] !== 'm') {
    return new GrammarError('语法错误', 'M标签应在path标签的第一个点');
  }

  for (let path of pathArray) {
    let pathName = path[0];
    let pathContent = path.substr(1, path.length - 1);
    let pathAttr = pathContent.split(/[ ]+/);
    for (let i = 0; i < pathAttr.length; i++) {
      if (pathAttr[i] === '') {
        pathAttr.splice(i, 1);
      }
    }
    let temp = null;
    switch (pathName) {
      case 'M':
        if (pathAttr.length !== 2) {
          return new GrammarError('M语法错误', 'M后应该跟随一个点');
        }
        point.push({
          x: parseFloat(pathAttr[0]),
          y: parseFloat(pathAttr[1]),
          flag: 'M'
        });
        break;
      case 'm':
        if (pathAttr.length !== 2) {
          return new GrammarError('m语法错误', 'm后应该跟随一个点');
        }
        temp = point[point.length - 1];
        point.push({
          x: temp.x + parseFloat(pathAttr[0]),
          y: temp.y + parseFloat(pathAttr[1]),
          flag: 'M'
        });
        break;
      case 'L':
        temp = point[point.length - 1];
        if (pathAttr.length !== 2) {
          return new GrammarError('L语法错误', 'L后应该跟随一个点');
        }
        point.push({
          x1: temp.x,
          y1: temp.y,
          x: parseFloat(pathAttr[0]),
          y: parseFloat(pathAttr[1]),
          flag: 'L'
        });
        break;
      case 'l':
        if (pathAttr.length !== 2) {
          return new GrammarError('l语法错误', 'L后应该跟随一个点');
        }
        temp = point[point.length - 1];
        point.push({
          x1: temp.x,
          y1: temp.y,
          x: temp.x + parseFloat(pathAttr[0]),
          y: temp.y + parseFloat(pathAttr[1]),
          flag: 'L'
        });
        break;
      case 'H':
        if (pathAttr.length !== 1) {
          return new GrammarError('H语法错误', 'H后应该跟随一个坐标');
        }
        temp = point[point.length - 1];
        point.push({
          x1: temp.x,
          y1: temp.y,
          x: parseFloat(pathAttr[0]),
          y: temp.y,
          flag: 'L'
        });
        break;
      case 'h':
        if (pathAttr.length !== 1) {
          return new GrammarError('h语法错误', 'h后应该跟随一个坐标');
        }
        temp = point[point.length - 1];
        point.push({
          x1: temp.x,
          y1: temp.y,
          x: temp.x + parseFloat(pathAttr[0]),
          y: temp.y,
          flag: 'L'
        });
        break;
      case 'V':
        if (pathAttr.length !== 1) {
          return new GrammarError('V语法错误', 'V后应该跟随一个坐标');
        }
        temp = point[point.length - 1];
        point.push({
          x1: temp.x,
          y1: temp.y,
          x: temp.x,
          y: parseFloat(pathAttr[0]),
          flag: 'L'
        });
        break;
      case 'v':
        if (pathAttr.length !== 1) {
          return new GrammarError('v语法错误', 'v后应该跟随一个坐标');
        }
        temp = point[point.length - 1];
        point.push({
          x1: temp.x,
          y1: temp.y,
          x: temp.x,
          y: temp.y + parseFloat(pathAttr[0]),
          flag: 'L'
        });
        break;
      case 'Q':
        if (pathAttr.length % 4 !== 0) {
          return new GrammarError('Q语法错误', 'Q后应该跟随两个点或两个点的倍数');
        }
        for (let i = 0; i < pathAttr.length / 4; i++) {
          point.push({
            x1: parseFloat(pathAttr[i * 4 + 0]),
            y1: parseFloat(pathAttr[i * 4 + 1]),
            x: parseFloat(pathAttr[i * 4 + 2]),
            y: parseFloat(pathAttr[i * 4 + 3]),
            flag: 'Q'
          });
        }
        break;
      case 'q':
        if (pathAttr.length % 4 !== 0) {
          return new GrammarError('q语法错误', 'q后应该跟随两个点或两个点的倍数');
        }
        for (let i = 0; i < pathAttr.length / 4; i++) {
          temp = point[point.length - 1];
          point.push({
            x1: temp.x + parseFloat(pathAttr[i * 4 + 0]),
            y1: temp.y + parseFloat(pathAttr[i * 4 + 1]),
            x: temp.x + parseFloat(pathAttr[i * 4 + 2]),
            y: temp.y + parseFloat(pathAttr[i * 4 + 3]),
            flag: 'Q'
          });
        }
        break;
      case 'C':
        if (pathAttr.length % 6 !== 0) {
          return new GrammarError('C语法错误', 'C后应该跟随三个点或其倍数');
        }
        for (let i = 0; i < pathAttr.length / 6; i++) {
          point.push({
            x1: parseFloat(pathAttr[i * 6 + 0]),
            y1: parseFloat(pathAttr[i * 6 + 1]),
            x2: parseFloat(pathAttr[i * 6 + 2]),
            y2: parseFloat(pathAttr[i * 6 + 3]),
            x: parseFloat(pathAttr[i * 6 + 4]),
            y: parseFloat(pathAttr[i * 6 + 5]),
            flag: 'C'
          });
        }
        break;
      case 'c':
        if (pathAttr.length % 6 !== 0) {
          return new GrammarError('C语法错误', 'C后应该跟随三个点或其倍数');
        }
        for (let i = 0; i < pathAttr.length / 6; i++) {
          temp = point[point.length - 1];
          point.push({
            x1: temp.x + parseFloat(pathAttr[i * 6 + 0]),
            y1: temp.y + parseFloat(pathAttr[i * 6 + 1]),
            x2: temp.x + parseFloat(pathAttr[i * 6 + 2]),
            y2: temp.y + parseFloat(pathAttr[i * 6 + 3]),
            x: temp.x + parseFloat(pathAttr[i * 6 + 4]),
            y: temp.y + parseFloat(pathAttr[i * 6 + 5]),
            flag: 'C'
          });
        }
        break;
      case 'S':
        if (pathAttr.length % 4 !== 0) {
          return new GrammarError('S语法错误', 'S后应该跟随两个点或二的倍数个点');
        }
        for (let i = 0; i < pathAttr.length / 4; i++) {
          temp = point[point.length - 1];
          let x1, y1;
          if (temp.flag === 'S' || temp.flag === 'C') {
            x1 = 2 * temp.x - temp.x2;
            y1 = 2 * temp.y - temp.y2;
          } else {
            x1 = parseFloat(pathAttr[i * 4 + 0]);
            y1 = parseFloat(pathAttr[i * 4 + 1]);
          }
          point.push({
            x1: x1,
            y1: y1,
            x2: parseFloat(pathAttr[i * 4 + 0]),
            y2: parseFloat(pathAttr[i * 4 + 1]),
            x: parseFloat(pathAttr[i * 4 + 2]),
            y: parseFloat(pathAttr[i * 4 + 3]),
            flag: 'C',
            S: 'S'
          });
        }
        break;
      case 's':
        if (pathAttr.length % 4 !== 0) {
          return new GrammarError('s语法错误', 'S后应该跟随两个点或二的倍数个点');
        }
        for (let i = 0; i < pathAttr.length / 4; i++) {
          temp = point[point.length - 1];
          let sx1, sy1;
          if (temp.flag === 'S' || temp.flag === 'C') {
            sx1 = 2 * temp.x - temp.x2;
            sy1 = 2 * temp.y - temp.y2;
          } else {
            sx1 = temp.x + parseFloat(pathAttr[i * 4 + 0]);
            sy1 = temp.y + parseFloat(pathAttr[i * 4 + 1]);
          }
          point.push({
            x1: sx1,
            y1: sy1,
            x2: temp.x + parseFloat(pathAttr[i * 4 + 0]),
            y2: temp.y + parseFloat(pathAttr[i * 4 + 1]),
            x: temp.x + parseFloat(pathAttr[i * 4 + 2]),
            y: temp.y + parseFloat(pathAttr[i * 4 + 3]),
            flag: 'C',
            S: 's'
          });
        }
        break;
      case 'T':
        if (pathAttr.length % 2 !== 0) {
          return new GrammarError('T语法错误', 'T后应该跟随一个点或一个点的倍数');
        }
        for (let i = 0; i < pathAttr.length / 2; i++) {
          temp = point[point.length - 1];
          let Tx1, Ty1;
          if (temp.flag === 'Q' || temp.flag === 'T') {
            Tx1 = 2 * temp.x - temp.x1;
            Ty1 = 2 * temp.y - temp.y1;
          } else {
            Tx1 = parseFloat(pathAttr[i * 2 + 0]);
            Ty1 = parseFloat(pathAttr[i * 2 + 1]);
          }
          point.push({
            x1: Tx1,
            y1: Ty1,
            x: parseFloat(pathAttr[i * 2 + 0]),
            y: parseFloat(pathAttr[i * 2 + 1]),
            flag: 'Q'
          });
        }
        break;
      case 't':
        if (pathAttr.length % 2 !== 0) {
          return new GrammarError('t语法错误', 't后应该跟随一个点或一个点的倍数');
        }
        for (let i = 0; i < pathAttr.length / 2; i++) {
          temp = point[point.length - 1];
          let tx1, ty1;
          if (temp.flag === 'Q' || temp.flag === 'T') {
            tx1 = 2 * temp.x - temp.x1;
            ty1 = 2 * temp.y - temp.y1;
          } else {
            tx1 = parseFloat(pathAttr[i * 2 + 0]) + temp.x;
            ty1 = parseFloat(pathAttr[i * 2 + 1]) + temp.y;
          }
          point.push({
            x1: tx1,
            y1: ty1,
            x: parseFloat(pathAttr[i * 2 + 0]) + temp.x,
            y: parseFloat(pathAttr[i * 2 + 1]) + temp.y,
            flag: 'Q'
          });
        }
        break;
      case 'A':
        if (pathAttr.length !== 7) {
          console.log('Aerror', path);
          console.log('AError', pathAttr);
          return new GrammarError('A语法错误', 'A后应该跟随7个属性');
        }
        temp = point[point.length - 1];
        // svg : [A | a] (rx ry x-axis-rotation large-arc-flag sweep-flag x y)
        const output = svgArcToCenterParam(
          temp.x,
          temp.y,
          parseFloat(pathAttr[0]),
          parseFloat(pathAttr[1]),
          parseFloat(pathAttr[2]),
          parseFloat(pathAttr[3]),
          parseFloat(pathAttr[4]),
          parseFloat(pathAttr[5]),
          parseFloat(pathAttr[6])
        );
        point.push({
          cx: output.cx,
          cy: output.cy,
          radiusX: parseFloat(pathAttr[0]),
          radiusY: parseFloat(pathAttr[1]),
          rotation: parseFloat(pathAttr[2]),
          startAngle: output.startAngle >= apprPI ? output.startAngle - apprPI : output.startAngle,
          endAngle: output.endAngle >= apprPI ? output.endAngle - apprPI : output.endAngle,
          clockwise: output.clockwise,
          x: parseFloat(pathAttr[5]),
          y: parseFloat(pathAttr[6]),
          flag: 'A'
        });
        break;
      case 'a':
        if (pathAttr.length !== 7) {
          console.log('Aerror', path);
          console.log('aError', pathAttr);
          return new GrammarError('a语法错误', 'a后应该跟随7个属性');
        }
        temp = point[point.length - 1];
        // svg : [A | a] (rx ry x-axis-rotation large-arc-flag sweep-flag x y)
        const outputa = svgArcToCenterParam(
          temp.x,
          temp.y,
          parseFloat(pathAttr[0]), // rx
          parseFloat(pathAttr[1]), // ry
          parseFloat(pathAttr[2]), // x-rotation
          parseFloat(pathAttr[3]), // large(1) small(0)
          parseFloat(pathAttr[4]), // clockwise
          temp.x + parseFloat(pathAttr[5]), // end-x
          temp.y + parseFloat(pathAttr[6]) // end-y
        );
        point.push({
          cx: outputa.cx,
          cy: outputa.cy,
          radiusX: parseFloat(pathAttr[0]),
          radiusY: parseFloat(pathAttr[1]),
          rotation: parseFloat(pathAttr[2]),
          startAngle: outputa.startAngle >= apprPI ? outputa.startAngle - apprPI : outputa.startAngle,
          endAngle: outputa.endAngle >= apprPI ? outputa.endAngle - apprPI : outputa.endAngle,
          clockwise: outputa.clockwise,
          x: temp.x + parseFloat(pathAttr[5]),
          y: temp.y + parseFloat(pathAttr[6]),
          flag: 'A'
        });
        break;
      case 'z':
        point.push({
          flag: 'Z'
        });
        break;
      case 'Z':
        point.push({
          flag: 'Z'
        });
        break;
    }
  }
  return point;
}

export {
  path
};
