<template>
  <div class="container-fluid framework">
    <div class="row header">
      <div class="col-6 left">代码区 Input Area</div>
      <div class="col-6 right">图形生成区 Output Area</div>
    </div>
    <div class="row content">
      <div class="col-6 code-area">
        <div class="row editor-wrapper">
          <div class="col-12">
            <codemirror class="editor" v-model="inputCode" :options="options"></codemirror>
          </div>
        </div>
        <div class="row test-wrapper">
          <button class="btn btn-outline-primary test" v-for="(code, index) in testCodes" :key="index" @click="inputCode = code.text">
            {{ code.name }}: {{ code.text }}
          </button>
        </div>
        <div class="button-group">
          <button class="btn btn-large btn-primary run" @click="showJson" :disabled="parseResult.length
           === 0">
            <i class="fa fa-jsfiddle"></i>&nbsp;&nbsp;&nbsp;Check JSON
          </button>
          <button class="btn btn-large btn-success run" @click="begin">
            <i class="fa fa-send"></i>&nbsp;&nbsp;&nbsp;Run Code
          </button>
        </div>
      </div>
      <div class="col-6 graphic-area">
        <transition name="fade">
          <div class="canvas-group" v-show="canvasShow">
            <v-canvas ref="canvas"></v-canvas>
          </div>
        </transition>
        <div class="button-group">
          <div class="ratio" @click="sizeChange(1.1)">
            <i class="fa fa-plus"></i>
          </div>
          <div class="ratio" @click="sizeChange(0.9)">
            <i class="fa fa-minus"></i>
          </div>
        </div>
        <div class="trashcan" @click="clearCanvas">
          <i class="fa fa-trash"></i>
        </div>
      </div>
    </div>
    <json-view ref="json" :json="{'parseResult': parseResult}"></json-view>
  </div>
</template>

<script>
import * as exception from '@/js/exception.js';
import { codemirror } from 'vue-codemirror';
require('codemirror/mode/htmlmixed/htmlmixed');
require('codemirror/addon/fold/foldcode.js');
require('codemirror/addon/fold/foldgutter.js');
require('codemirror/addon/fold/brace-fold.js');
require('codemirror/addon/fold/xml-fold.js');
require('codemirror/addon/fold/indent-fold.js');
require('codemirror/addon/fold/markdown-fold.js');
require('codemirror/addon/fold/comment-fold.js');

import Vcanvas from '@/components/Vcanvas';
import Json from '@/components/Json';

import { path } from '@/js/path';
import { codes } from '@/js/testCode';

export default {
  components: {
    codemirror,
    'v-canvas': Vcanvas,
    'json-view': Json
  },
  data() {
    return {
      inputCode: '',
      testCodes: [],
      options: {
        mode: 'htmlmixed',
        indentUnit: 1,
        indentWithTabs: true,
        lineNumbers: true,
        theme: 'twilight'
      },
      parseResult: [],
      ratio: 1, // 图形放大缩小比例
      canvasShow: false
    };
  },
  created () {
    this.testCodes = codes;
    CanvasRenderingContext2D.prototype.ellipse = function (x, y, radiusX, radiusY, rotation, startAngle, endAngle, antiClockWise) {
      this.save();
      this.translate(x, y);
      this.rotate(rotation / 360 * 2 * Math.PI);
      this.scale(radiusX, radiusY);
      this.arc(0, 0, 1, startAngle, endAngle, !antiClockWise);
      this.restore();
    }
  },
  methods: {
    // 开始
    begin() {
      this.canvasShow = false;
      this.clearCanvas();      
      setTimeout(() => {
        this.parseCode();
        this.canvasShow = true;
      }, 200);
    },
    // 解析代码
    parseCode() {
      if ('' === this.inputCode) {
        alert('请输入代码');
        return;
      } else {
        let result;
        try{
          result = exception.isRight(this.inputCode);
        }
        catch(e){
          alert(e.print());
          return;
        }
      }
      // 单标签匹配
      let codeSingleArray = this.delSpace(this.inputCode);
      codeSingleArray.forEach(code => {
        let parse = {};
        // 获取标签名称
        let title = code.match(/<\/?[a-zA-Z]+/g)[0];
        title = title.substring(1, title.length);
        // 获取属性数组
        let array = code.match(/[a-zA-Z0-9-]+=".+?"/g);
        //获取text
        if ('text' === title || 'tspan' === title) {
          if (/<\/[a-zA-Z0-9]+>/.test(code)) title = "/" + title;
          let characters = code.split(">")[1];
          characters = characters.split("<")[0];
          array.push('characters="' + characters + '"');
          parse.style = {'font-size': '24px'};
        }
        if(code[code.length-2] === "/"){
          parse['pair'] = false
        }else{
          parse['pair'] = true
        }
        if (array) {
          for (let i = 0; i < array.length; i++) {
            let pair = array[i].split("=");
            let value = pair[1];
            let realValue = value.substring(1, value.length - 1);
            // 如果属性名是 points
            if ('d' === pair[0]) {
              if ('path' !== title) {
                // 报错
              } else {
                parse['points'] = path(realValue);
              }
            } else if ('points' === pair[0]) {
              // 各个坐标点分开
              let pointsStringArray = realValue.split(' ');
              // 储存每个坐标点数据
              let pointsArray = [];
              // 对于每个字符串形式的坐标点
              pointsStringArray.forEach(pointsString => {
                let xy = pointsString.split(",");
                pointsArray.push({
                  x: parseInt(xy[0]),
                  y: parseInt(xy[1])
                });
              });
              parse['points'] = pointsArray;
            } else if ('style' === pair[0]) {
              // 如果属性名是 style
              let style = {};
              let styleInput = pair[1].substring(1, pair[1].length - 1);
              let styleOrigin = styleInput.split(";");
              for (let j = 0; j < styleOrigin.length; ++j) {
                let temp = styleOrigin[j].split(":");
                if (temp.length > 1) {
                  if (temp[1].match(/[0-9]+$/) && temp[1][0] !== '#') {
                    temp[1] = parseInt(temp[1]);
                  }
                  style[temp[0]] = temp[1];
                }
              }
              parse["style"] = style;
            } else if(realValue.match(/,[0-9]+/)){
              parse[pair[0]] = realValue;
            } else if (realValue.match(/[0-9]+$/) && realValue[0] !== '#' && pair[0] !== 'characters') {
              // 如果属性值是数字
              parse[pair[0]] = parseInt(realValue);
            } else {
            // 否则是字符串
            parse[pair[0]] = realValue;
            }
          }
        }
        // 合并并加入数组
        this.parseResult.push(Object.assign({}, { title: title}, parse));
      });
      this.operate();
    },
    /**
    * 删除输入串的换行和注释
    * @method delSpace
    * @param {string} inputCode 当前处理代码段
    * @return {string} 处理结束后的代码段
    */
    delSpace(inputCode){
      let codeSplit = this.inputCode.split('\n');
      let iflag = 0;
      let codeSingleArray = '';
      for(let i = 0;i < codeSplit.length;i++) {
        if(iflag === 1)
          i--;
        if((/<!--.*-->/.test(codeSplit[i])) === true){
          codeSplit.splice(i,1);
          iflag = 1;
          continue;
        }
        if((/<.+>.*/.test(codeSplit[i])) === false){
          codeSplit[i] += codeSplit[i+1];
          codeSplit.splice(i+1,1);
          iflag = 1;
        }
        iflag = 0;
      }

      for(let j = 0;j < codeSplit.length;j++){
         codeSingleArray += codeSplit[j] + '\n';
      }
      codeSingleArray = codeSingleArray.match(/<.+>.*/g);
      return codeSingleArray;
    },
    // 将解析的代码进行绘制操作
    operate() {
      let idGradient;
      let judgeDoubleLabel = 0;
      let judgeInTextDul = 0;
      let parentColor = "transparent";
      let parentFontSize = "transparent";
      let tempShapeData = {};
      this.parseResult.forEach(result => {
        result = this.unifyStyle(result);
        switch (result.title) {
          case 'circle':
            if ((judgeDoubleLabel === 0) && (judgeInTextDul === 0)) {
              if(result.pair === true) {
                tempShapeData['type'] = 'circle';
                tempShapeData['cx'] = result['cx'];
                tempShapeData['cy'] = result['cy'];
                tempShapeData['r'] = result['r'];
                tempShapeData['stroke'] = result['style']['stroke'];
                tempShapeData['stroke-width'] = result['style']['stroke-width'];
                tempShapeData['fill'] = result['style']['fill'];
              }else {
                this.$refs.canvas.drawCircle(
                    result['cx'],
                    result['cy'],
                    result['r'],
                    result['style']['stroke'],
                    result['style']['stroke-width'],
                    result['style']['fill']
                );
              }
            }
            break;
          case 'polyline':
            if ((judgeDoubleLabel === 0) &&(judgeInTextDul === 0)) {
              this.$refs.canvas.drawPoly(
                result['points'],
                result['style']['stroke'],
                result['style']['stroke-width'],
                result['style']['fill'],
                0
              );
            }
            break;
          case 'polygon':
            if ((judgeDoubleLabel === 0) &&(judgeInTextDul === 0)) {
              this.$refs.canvas.drawPoly(
                result['points'],
                result['style']['stroke'],
                result['style']['stroke-width'],
                result['style']['fill'],
                1
              );
            }
            break;
          case 'rect':
            if ((judgeDoubleLabel === 0) &&(judgeInTextDul === 0)) {
              if(result.pair === true) {
                tempShapeData['type'] = 'rect';
                tempShapeData['x'] = result['x'];
                tempShapeData['y'] = result['y'];
                tempShapeData['width'] = result['width'];
                tempShapeData['height'] = result['height'];
                tempShapeData['stroke'] = result['style']['stroke'];
                tempShapeData['stroke-width'] = result['style']['stroke-width'];
                tempShapeData['fill'] = result['style']['fill'];
              }else {
                this.$refs.canvas.drawRectangle(
                    result['x'],
                    result['y'],
                    result['width'],
                    result['height'],
                    result['style']['stroke'],
                    result['style']['stroke-width'],
                    result['style']['fill']
                );
              }
            }
            break;
          case 'line':
            if ((judgeDoubleLabel === 0) &&(judgeInTextDul === 0)) {
                this.$refs.canvas.drawLine(
                    result['x1'],
                    result['y1'],
                    result['x2'],
                    result['y2'],
                    result['style']['stroke'],
                    result['style']['stroke-width']
                );
            }
            break;
          case 'ellipse':
            if ((judgeDoubleLabel === 0) &&(judgeInTextDul === 0)) {
              if(result.pair === true) {
                tempShapeData['type'] = 'ellipse';
                tempShapeData['cx'] = result['cx'];
                tempShapeData['cy'] = result['cy'];
                tempShapeData['rx'] = result['rx'];
                tempShapeData['ry'] = result['ry'];
                tempShapeData['stroke'] = result['style']['stroke'];
                tempShapeData['stroke-width'] = result['style']['stroke-width'];
                tempShapeData['fill'] = result['style']['fill'];
              }else {
                this.$refs.canvas.drawEllipse(
                  result['cx'],
                  result['cy'],
                  result['rx'],
                  result['ry'],
                  result['style']['stroke'],
                  result['style']['stroke-width'],
                  result['style']['fill']
                );
              }
            }
            break;
          case '/path':
            if ((judgeDoubleLabel === 0) &&(judgeInTextDul === 0)) {
              this.$refs.canvas.drawPath(
                result['points'],
                result['style']['stroke'],
                result['style']['stroke-width'],
                result['style']['fill'],
              );
            }
            break;
          case 'path':
            this.$refs.canvas.drawPath(
              result['points'],
              result['style']['stroke'],
              result['style']['stroke-width'],
              result['style']['fill']
            );
            break;
          case 'linearGradient':
            if ((judgeDoubleLabel === 0) &&(judgeInTextDul === 0)) {
              judgeDoubleLabel = 1;
              idGradient = result['id'];
              this.$refs.canvas.createLinearGradient(
                result['id'],
                result['x1'],
                result['y1'],
                result['x2'],
                result['y2']
              );
            }
            break;
          case "radialGradient":
            if ((judgeDoubleLabel === 0) &&(judgeInTextDul === 0)) {
              judgeDoubleLabel = 1;
              idGradient = result["id"];
              this.$refs.canvas.createRadialGradient(
                  result["id"],
                  result["cx"] || null,
                  result["cy"] || null,
                  result["r"] || null,
                  result["fx"] || null,
                  result["fy"] || null
              );
            }
            break;
          case 'stop':
            if ((judgeDoubleLabel === 1) && (judgeInTextDul === 0)) {
              this.$refs.canvas.drawGradient(
                idGradient,
                result['offset'],
                result['style']['stop-color']
              );
            }
            break;
          case "/linearGradient":
            judgeDoubleLabel = 0;
            idGradient = [];
            break;
          case "/radialGradient":
            judgeDoubleLabel = 0;
            idGradient = [];
            break;
          case 'text':
            if ((judgeDoubleLabel === 0) && (judgeInTextDul === 0)) {
              judgeDoubleLabel = 1;
              parentColor = result['style']['fill'];
              parentFontSize = result['style']['font-size'];
              this.useDrawText(result,parentColor,parentFontSize)
            }
            break;
           case 'tspan':
           if ((judgeDoubleLabel === 1) && (judgeInTextDul === 0)) {
             judgeInTextDul = 1
             this.useDrawText(result,parentColor,parentFontSize)
            }
            break;
          case '/text':
            judgeDoubleLabel = 0;
            this.useDrawText(result,parentColor,parentFontSize)
            parentColor = 'transparent'
            parentFontSize = 'transparent'
            break;
          case '/tspan':
              judgeInTextDul = 0
              this.useDrawText(result,parentColor,parentFontSize)
            break;
          case 'animate':
              this.$refs.canvas.animate(
              result['attributeName'],
              result['from'],
              result['to'],
              result['dur'],
            );
            break;
          case '/circle':
            this.$refs.canvas.addAnimate(
                tempShapeData
            );
            tempShapeData = [];
            break;
          case '/ellipse':
            this.$refs.canvas.addAnimate(
                tempShapeData
            );
            tempShapeData = [];
            break;
          case '/rect' :
            this.$refs.canvas.addAnimate(
                tempShapeData
            );
            tempShapeData = [];
            break;
          default:
            console.log(result.title); // DO NOTHING
        }
      });
      this.$refs.canvas.drawAnimate();
    },
    useDrawText(result, parentColor,parentFontSize) {
      if(result['style']['font-size'] === "transparent")
        result['style']['font-size'] = parentFontSize;
      if(result['style']['fill'] === "transparent")
        result['style']['fill'] = parentColor;
      this.$refs.canvas.drawText(
        result['x'],
        result['y'],
        result['style']['fill'],
        result['characters'],
        result['style']['font-size']
      );
    },
    // 将样式统一处理，方便传参
    unifyStyle(json) {
      let style = {
        'fill': json['fill'] ? json['fill'] : 'transparent',
        'stroke': json['stroke'] ? json['stroke'] : null,
        'stroke-width': json['stroke-width'] ? json['stroke-width'] : null,
      };

      json.style = Object.assign({}, style, json.style);
      return json;
    },
    // 清空画板
    clearCanvas() {
      this.canvasShow = false;
      this.parseResult = [];
      this.$refs.canvas.clearCanvasComplete();
    },

    // 放大或缩小
    sizeChange(ratio) {
      this.parseResult.forEach(result => {
        this.sizeChangeWork(ratio, result);
      });
      this.$refs.canvas.clearCanvasComplete();
      this.operate();
    },

    // 遍历所有数字属性的方式进行放大或缩小，递归方式操作嵌套对象
    sizeChangeWork(ratio, json) {
      for (let key in json) {
        if (typeof json[key] === 'number' || /\d+px/.test(json[key])) {
          if(key.length <= 2 || key.indexOf('radius') !== -1 || key.indexOf('width') !== -1 || key.indexOf('height') !== -1 || key.indexOf('from') !== -1){
            json[key] *= ratio;
          }
          if (/\d+px/.test(json[key])) {
            json[key] = parseInt(json[key].match(/\d+/)) * ratio + 'px';
          }
        } else if (typeof json[key] === 'object') {
          this.sizeChangeWork(ratio, json[key]);
        }
      }
    },
    // 展示 JSON 结构
    showJson() {
      this.$refs['json'].show();
    }
  }
};
</script>

<style lang="stylus" scoped>
.framework
  height: 100%
  .header
    position: relative
    height: 50px
    line-height: 50px
    text-align: center
    font-size: 24px
    font-weight: bold
    z-index: 99
    .left
      background-color: #1c2128
      border-bottom: 1px solid #ccc
      color: #faffeb
    .right
      border-bottom: 1px solid #ccc
      background-color: #faffeb
      color: #1f6fb5
  .content
    height: calc(100vh - 50px)
    .code-area
      position: relative
      padding: 0
      z-index: 99
      border-right: 1px solid #20262e
      background-color: #1c2128
      text-align: center
      .editor-wrapper
        text-align: left
        height: 50%
        .editor 
          width: 100%
          height: 100%
          padding: 10px
          font-size: 18px
          font-family: Consolas, Courier, monospace
      .test-wrapper
        text-align: center
        height: 30%
        width: 100%
        padding-left: 20px
        margin-top: 20px
        overflow-x: hidden
        overflow-y: auto
        .test
          width: 90%
          overflow: hidden
          text-overflow: ellipsis
          margin: 10px auto
      .button-group
        display: inline-block
        position: absolute
        right: 20px
        bottom: 20px
        outline: none
        box-shadow: none
        .run
          margin-left: 10px
    .graphic-area
      position: relative
      margin: 0
      padding: 0
      background-image: url('canvas.gif')
      background-size: cover
      background-repeat: no-repeat
      .fade-enter-active, .fade-leave-active
        transition: all 0.2s
      .fade-enter, .fade-leave-to
        opacity: 0 
      .button-group
        position: absolute
        z-index: 100
        left: 20px
        bottom: 15px
        height: 80px
        line-height: 80px
        .ratio
          display: inline-block
          width: 50px
          height: 50px
          line-height: 50px
          margin-right: 10px
          border-radius: 50%
          border: 1px solid #007bff
          vertical-align: middle
          text-align: center
          font-size: 30px
          color: #007bff
          transition: all 0.2s ease-in-out
          &:hover
            cursor: pointer
            width: 80px
            height: 80px
            line-height: 80px
            background-color: #007bff
            font-size: 60px
            color: #eee
      .trashcan
        position: absolute
        z-index: 100
        right: 20px
        bottom: 15px
        width: 70px
        height: 70px
        line-height: 70px
        border-radius: 50%
        text-align: center
        font-size: 40px
        background-color: #ccc
        transition: all 0.2s ease-in-out
        &:hover
          width: 150px
          height: 150px
          line-height: 150px
          cursor: pointer
          color: #eee
          background-color: #c82333
</style>
