import { Error } from '../lib/error';

let pairLabel = ['text', 'tspan', 'a', 'g', 'def', 'linearGradient', 'circle', 'line', 'rect', 'ellipse', 'polyline', 'polygon', 'path', 'defs', 'radialGradient'];
// let pairInnerLabel = [['tspan', 'textPath'], [], ['text'], [], ['path', 'linearGradient', ''], ['stop']]
let unpairLabel = ['stop', 'circle', 'line', 'rect', 'ellipse', 'polyline', 'polygon', 'path', 'animate'];
let labels = [];
var assert = require('assert');

/**
* 栈.
* @class Stack
* @constructor
* @param {int*} items - 栈内存储的信息.
*/
function Stack () {
  let items = [];// 存储栈

  // 添加一个或多个元素到栈顶
  this.push = function (element) {
    items.push(element);
  };

  // 移除栈顶的元素，并且返回被移除的元素
  this.pop = function () {
    return items.pop();
  };

  // 返回栈顶的元素，不做任何操作
  this.peek = function () {
    return items[items.length - 1];
  };

  // 检测栈里是否有元素，没有true，有false
  this.isEmpty = function () {
    return items.length === 0;
  };

  // 清空栈
  this.clear = function () {
    items = [];
  };

  // 返回栈里的元素个数
  this.size = function () {
    return items.length;
  };
}

// 尖括号栈
let bracStack = new Stack();
// 标签栈
let labelStack = new Stack();

/**
* 判断输入的格式是否符合svg格式
* @method isRight
* @param {string} inputCode 当前处理代码段
* @return {true} 若代码无错，返回true,否则抛出异常
*/
function isRight (inputCode) {
  //assert(checkLBrac('<','>',1),'括号不匹配');
  //return true;
  let line = 1;// 第几行出错
  let codeArray = inputCode.split('\n');
  let first = '';
  let re = '';

  // 判断第一个是否是<或注释
  if (!(/<\/?[a-zA-Z]+|\/?>|<!--/g.test(codeArray[0]))) {
    console.log('wrong');
  }
  first = codeArray[0].match(/<\/?[a-zA-Z]+|-->|<!--|\/?>/g)[0];
  if (/<[a-zA-Z]+/.test(first)) {
    bracStack.push('<');
    let temp = first.substring(1, first.length);
    labelStack.push(temp);
  } else if (/<!--/.test(first)) {
    bracStack.push('<!--');
  } else {
    let errors = new Error(1, first, '括号不匹配');
    throw errors;
    //return errors;
  }

  // 按行分析
  let code = '';
  for (let j = 0; j < codeArray.length; j++) {
    code = codeArray[j];
    re = formatSvg(code, line);
    if (re !== true) {
      return re;
    }
    line++;
  }

  // 分析结束栈是否为空
  if ((bracStack.isEmpty() === true) && (labelStack.isEmpty() === true)) {
    return true;
  } else {
    if ((bracStack.peek() === '<') || (bracStack.peek() === '</')) {
      let errors = new Error(line - 1, bracStack.pop() + labelStack.pop(), '括号不匹配2');
      throw errors;
    }
    let errors = new Error(line - 1, bracStack.pop(), '括号不匹配');
    throw errors;
  }
}

/**
* 通过栈对每一行判断括号和标签是否匹配
* @method formatSvg
* @param {string} code 当前处理代码段
* @param {int} line 当前处理代码行数
* @return {true} 若代码无错，返回true,否则抛出异常
*/
function formatSvg (code, line) {
  if (!(/<\/?[a-z]+|\/?>/g.test(code))) {
    return true;
  }
  code = code.match(/<\/?[a-zA-Z]+|\/?>|-->|<!--|'|"/g);
  if (line === 1) {
    code.shift();
  }

  // 对每一个标号分析
  let last = '';
  let chars = '';
  let re = '';
  for (let i = 0; i < code.length; i++) {
    chars = code[i];
    if (bracStack.isEmpty() === false) {
      last = bracStack.peek();
    }
    // 当遇到<
    if (/<[a-zA-Z]+/.test(chars)) {
      re = checkLBrac(chars, last, line);
    } else if (/<!--/.test(chars)) {
      // 当遇到<!--
      re = checkLAnnota(chars, last, line);
    } else if (/-->/.test(chars)) {
      // 当遇到-->
      re = checkRAnnota(chars, last, line);
    } else {
      if ((bracStack.isEmpty() === true) || (labelStack.isEmpty() === true)) {
        let errors = new Error(line, chars, '括号不匹配');
        throw errors;
      } else {
        last = bracStack.peek();
        re = checkDetail(chars,last,line);
      }
    }
    if (re !== true) { return re; }
  }
  return true;
}

//具体检查调用
function checkDetail(chars,last,line){
  let re = '';
  if (/\/>/.test(chars)) {
    re = checkRSlashBrac(chars, last, line);
  } else if (/<\/[a-zA-Z]+/.test(chars)) {
    re = checkLBracSlash(chars, last, line);
  } else if (/>/.test(chars)) {
    re = checkRBrac(chars, last, line);
  } else if (chars === '"') {
    re = checkDualQuoation(chars, last, line);
  } else if (chars === "'") {
    re = checkSingleQuoation(chars, last, line);
  } else {
    console.log('非预期错误');
  }
  return re;
}

/**
 * 当符号为<时进行判错
 * @method checkLBrac
 * @param {string} chars 当前处理的字符
 * @param {string} last 当前符号栈栈顶存储的元素
 * @param {int} line 当前判断的行数
 * @return {true|error} 当前符号无错时，返回true；若出错，返回error类型
 */
function checkLBrac (chars, last, line) {
  if (bracStack.isEmpty() === true) {
    bracStack.push('<');
    labelStack.push(chars.substring(1, chars.length));
    return true;
  }
  last = bracStack.peek();
  if (last === '>') {
    bracStack.push('<');
    labelStack.push(chars.substring(1, chars.length));
    return true;
  } else {
    let errors = new Error(line, chars, '<括号不匹配');
    throw errors;
  }
}

// 符号为/>
function checkRSlashBrac (chars, last, line) {
  if ((last === '<') && (unpairLabel.indexOf(labelStack.peek()) !== -1)) {
    labels.push(labelStack.peek());
    bracStack.pop();
    labelStack.pop();
    return true;
  } else {
    let errors =  new Error(line, chars, '/>括号不匹配');
    throw errors;
  }
}

// 符号为</
function checkLBracSlash (chars, last, line) {
  if ((last === '>') && (pairLabel.indexOf(labelStack.peek()) !== -1) && (pairLabel.indexOf(chars.substring(2, chars.length)) !== -1)) {
    bracStack.push('</');
    return true;
  } else {
    let errors = new Error(line, chars, '</括号不匹配');
    throw errors;
  }
}

// 符号为-->
function checkRAnnota (chars, last, line) {
  if (last === '<!--') {
    bracStack.pop();
    return true;
  } else {
    let errors = new Error(line, chars, '-->括号不匹配');
    throw errors;
  }
}

// 符号为>
function checkRBrac (chars, last, line) {
  if (last === '</') {
    labels.push('/' + labelStack.peek());
    labelStack.pop();
    bracStack.pop();
    bracStack.pop();
    bracStack.pop();
    return true;
  } else if (last === '<') {
    if (pairLabel.indexOf(labelStack.peek()) !== -1) {
      labels.push('/' + labelStack.peek());
      bracStack.push('>');
      return true;
    }
  } else {
    let errors = new Error(line, chars, '>括号不匹配');
    throw errors;
  }
}

// 符号为<!--
function checkLAnnota (chars, last, line) {
  if ((last === '>') || (bracStack.isEmpty() === true) || (labelStack.isEmpty() === true)) {
    bracStack.push('<!--');
    return true;
  } else {
    let errors = new Error(line, chars, '<!--括号不匹配');
    throw errors;
  }
}

// 符号为"
function checkDualQuoation (chars, last, line) {
  if ((last === '"')) {
    bracStack.pop();
    return true;
  } else {
    bracStack.push('"');
    return true;
  }
}

// 符号为'
function checkSingleQuoation (chars, last, line) {
  if ((last === "'")) {
    bracStack.pop();
    return true;
  } else {
    bracStack.push("'");
    return true;
  }
}

export { isRight, formatSvg, labels };
